import { Component, OnInit } from '@angular/core';
import { ProductService } from "../../shared/services/product.service";
import { Product } from "../../shared/classes/product";
import { ApiCartService } from 'src/app/shared/api-services/cart.service';
import { IPayPalConfig, ICreateOrderRequest } from 'ngx-paypal';
import { faTrash } from '@fortawesome/free-solid-svg-icons';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit {
  public cart: any = null;
  public products: Product[] = [];
  public payPalConfig: IPayPalConfig|null = null;
  public shippingCost: number = 10;
  public faTrash = faTrash;
  public couponCode: string = '';

  constructor(
    public productService: ProductService,
    private apiCartService: ApiCartService,
    private router: Router,
  ) {
  }

  ngOnInit(): void {
    this.apiCartService.cart.subscribe(
      (cart) => {
        if(cart != null) {
          this.cart = cart;
          this.products = cart.products;
          this.initPayPalConfig();
        }
      }
    );
  }

  // Increament
  increment(product) {
    product.quantity = product.quantity + 1;
  }

  // Decrement
  decrement(product) {
    if(product.quantity > 1) {
      product.quantity = product.quantity - 1;
    }
  }

  public removeItem(product: any) {
    this.apiCartService.removeProductToShoppingCart(product.product.id, product.size).subscribe();
  }

  /**
   * Returns a number of the total price of cart (shipping cost not included)
   * 
   * @returns number
   */
  public getSubTotal(): number {
    let subTotal = 0;
    this.products.forEach(
      (cartProduct: any) => {
        subTotal += cartProduct.product.price * cartProduct.quantity;
      }
    );

    return subTotal;
  }

  /**
   * Returns the total price including the shipping cost
   * 
   * @returns string
   */
  public getTotal(withShipping: boolean = true, withCoupon: boolean = true): number {
    let tempShippingCost: number = withShipping ? this.shippingCost : 0;
    let subTotal = this.getSubTotal();
    if(withCoupon && this.cart.applied_coupon != null) {
      if(this.cart.applied_coupon.coupon.type === 'fixed') {
        return ((subTotal - this.cart.applied_coupon.coupon.amount_off) + tempShippingCost);
      } else {
        let percentage = parseFloat(0 + this.cart.applied_coupon.coupon.amount_off);
        let toSubtract = subTotal * percentage;
        return (subTotal - toSubtract + tempShippingCost);
      }
    } else {
      return (subTotal + tempShippingCost);
    }
  }

  public onGoToCheckout() {
    this.apiCartService.updateProductsToShoppingCart().subscribe(
      () => {
        this.router.navigate(['/shop/checkout']);
      }
    );
  }

  private initPayPalConfig(): void {
    let items_array = [];
    this.products.forEach(
      (product) => {
        let temp = {
          name: product.product.title,
          quantity: product.quantity,
          category: 'PHYSICAL_GOODS',
          unit_amount: {
              currency_code: 'EUR',
              value: product.product.price,
          },
      };

        items_array.push(temp);
      }
    );

    this.payPalConfig = {
        fundingSource: 'PAYPAL',
        currency: 'EUR',
        clientId: 'AcQzL89yJEwuDuVH4K5DD0agEM-QLpiOE1CaKbIkYNhsNYQyUCtxz58zLrQohkiK0TGNThVYfvPvuOyg',
        createOrderOnClient: (data) => < ICreateOrderRequest > {
            intent: 'CAPTURE',
            purchase_units: [{
                amount: {
                    currency_code: 'EUR',
                    value: this.getTotal(false, true).toFixed(0),
                    breakdown: {
                        item_total: {
                            currency_code: 'EUR',
                            value: this.getTotal(false, false).toFixed(0),
                        },
                        // discount: {
                        //     currency_code: 'EUR',
                        //     value: '10',
                        // }
                    }
                },
                items: items_array,
            }]
        },
        advanced: {
            commit: 'true'
        },
        style: {
            label: 'paypal',
            layout: 'vertical'
        },
        onApprove: (data, actions) => {
            console.log('onApprove - transaction was approved, but not authorized', data, actions);
            actions.order.get().then(details => {
                console.log('onApprove - you can get full order details inside onApprove: ', details);
            });

        },
        onClientAuthorization: (data) => {
            console.log('onClientAuthorization - you should probably inform your server about completed transaction at this point', data);
            this.apiCartService.savePayPalOrderData(data).subscribe(
              (response) => {
                console.log('response')
                console.log(response)
              }
            );
            // this.showSuccess = true;
        },
        onCancel: (data, actions) => {
            console.log('OnCancel', data, actions);
            // this.showCancel = true;

        },
        onError: err => {
            console.log('OnError', err);
            // this.showError = true;
        },
        onClick: (data, actions) => {
            console.log('onClick', data, actions);
            // this.resetStatus();
        }
    };
  }

}
